.contacts {
  border: 2px solid rgba(0, 0, 0, 0.125);
  border-radius: 24px;
  padding: 12px 24px;
  width: 860px;

  &__photo-container.card-text {
    height: 60px;
    min-width: 60px;
    margin-right: 12px;
  }

  &__photo {
    height: 100%;
    width: 100%;
    border-radius: 50%;
  }

  &__name.card-text {
    display: flex;
    font-weight: bold;
    width: 140px;
    overflow-x: hidden;
  }

  &__phone {
    display: flex;
    text-decoration: none;
    font-weight: bold;
    color: #0b5ed7;
    transition: color 500ms;

    &:hover {
      color: rgb(212, 101, 101);
    }
  }

  &__top-bar {
    margin-bottom: 12px;
  }

  &__button-container {
    display: flex;
    gap: 4px;
  }
}

.card {
  margin-bottom: 4px;
  border-radius: 24px;
}

.card-body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
}

.card-title,
.card-text {
  margin: 0;
  margin-right: 4px;
}

.btn.btn-secondary,
.btn.btn-success,
.btn.btn-primary {
  border-radius: 24px;
  width: 80px;
}
